<template>
  <v-card>
    <v-card-title>
      <v-icon color="opcion1" class="mx-2">mdi-clipboard-text-clock</v-icon>
       <span>Historial Tickets</span>

        <v-divider  class="mx-2" inset vertical></v-divider>

         <v-flex  class="mx-2"  xs5 sm4 md5 lg4>
          <v-text-field  justify-center
                                v-model="search"
                                append-icon="mdi-magnify"
                                label="Busqueda"
                                single-line
                                hide-details
                              ></v-text-field> 

          </v-flex>

           <vue-excel-xlsx v-show="ticketsArray.length >0" class="mx-2"
              :data="ticketsArray"
              :columns="column_excel"
              :filename="'HistorialTicket'"
              :sheetname="'hoja1'">
              <v-btn color="info">
                  <v-icon dark>get_app</v-icon>
              </v-btn>
            
              </vue-excel-xlsx>


        <v-spacer></v-spacer>

        <span class="text-caption">{{ fecha_inicio }}</span>/<span class="text-caption">{{ fecha_fin }}</span>
         
        <v-btn fab  small color="opcion2" dark class="mx-2" @click="dialogInicio=true"><v-icon>today</v-icon></v-btn>



    </v-card-title>

  <v-data-table
    :headers="headers"
    :items="ticketsArray"
    :search="search"
    sort-by="fecha_reportado"
    class="elevation-1 mt-5 mx-1"
  >
       
    <template v-slot:[`item.actions`]="{ item }">
      <v-icon  color="green" small class="mr-2" @click="editItem(item)">
       mdi-pencil
      </v-icon>
      <v-icon  color="red" small @click="deleteItem(item)">
        mdi-delete
      </v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="listar()">Reset</v-btn>
    </template>

    <template v-slot:[`item.fecha_reportado`]="{ item }">
          <span class="text-caption">{{ getDate(item.fecha_reportado) }}</span>
    </template>

      <template v-slot:[`item.fecha_solucion`]="{ item }">
          <span class="text-caption">{{ getDate(item.fecha_solucion) }}</span>
    </template>



    <template  v-slot:[`item.fecha_llegada_tenico`]="{ item }">
          <span v-if="item.fecha_llegada_tenico && item.fecha_llegada_tenico!=''" class="text-caption">{{ getDate(item.fecha_llegada_tenico) }}</span>
    </template>

    <template  v-slot:[`item.fecha_inicio_tenico`]="{ item }">
          <span v-if="item.fecha_inicio_tenico && item.fecha_inicio_tenico!=''" class="text-caption">{{ getDate(item.fecha_inicio_tenico) }}</span>
    </template>

    <template v-slot:[`item.fecha_fin_tecnico`]="{ item }">
          <span   v-if="item.fecha_fin_tecnico && item.fecha_fin_tecnico!=''" class="text-caption">{{ getDate(item.fecha_fin_tecnico) }}</span>
    </template>

    

   <template v-slot:[`item.id_tipo_ticket.nombre_tipo_ticket`]="{ item }">
          <v-chip color="red" v-if=" item.id_tipo_ticket.nombre_tipo_ticket=='Reporte fallas'" class="white--text" small>{{ item.id_tipo_ticket.nombre_tipo_ticket }}</v-chip>
          <v-chip v-else color="blue" class="white--text"  small>{{ item.id_tipo_ticket.nombre_tipo_ticket }}</v-chip>
   </template>

   <template v-slot:[`item.id_tipo_trabajo.nombre_trabajo`]="{ item }">
          <v-chip  outlined color="red" v-if=" item.id_tipo_trabajo.nombre_trabajo=='Revision fisica'" class="white--text" x-small>{{ item.id_tipo_trabajo.nombre_trabajo }}</v-chip>
          <v-chip  outlined v-else color="blue" class="white--text"  x-small>{{ item.id_tipo_trabajo.nombre_trabajo }}</v-chip>
    </template>

    <template v-slot:[`item.id_equipo`]="{ item }">
          <span class="text-caption">{{ item.id_equipo }}</span>
    </template>

    <template v-slot:[`item.nombre_codigo`]="{ item }">
          <span class="text-caption">{{ item.nombre_codigo }}</span>
    </template>

    <template v-slot:[`item.grupo`]="{ item }">
          <span class="text-caption">{{ item.grupo }}</span>
    </template>

    <template v-slot:[`item.id_tipo_vehiculo.tipo_vehiculo`]="{ item }">
        <span class="text-caption">{{ item.id_tipo_vehiculo.tipo_vehiculo }}</span>
    </template>

    <template v-slot:[`item.descripcion`]="{ item }">
        <span class="text-caption">{{ item.descripcion }}</span>
    </template>

    <template v-slot:[`item.comentario_atc`]="{ item }">
        <span class="text-caption">{{ item.comentario_atc }}</span>
    </template>

   <template v-slot:[`item.cumple_sla`]="{ item }">
          <v-icon>
            {{ item.cumple_sla==1 ? "mdi-check" : "mdi-alert-circle" }}
          </v-icon>
    </template>



     <template v-slot:[`item.status`]="{ item }">
        <v-chip color="success" v-if="item.status==1" small>Solucionado</v-chip>
        <v-chip color="red" v-if="item.status==2" small>Vehiculo no disponible</v-chip>
    </template>
    




 
  </v-data-table>
  


 

    <v-dialog v-model="dialogInicio" persistent max-width="280">
        <v-card>
          <v-toolbar flat color="opcion1" class="text-center">
          <v-toolbar-title class="white--text  text-center"><strong>Tickets por fechas</strong></v-toolbar-title>
        </v-toolbar>

          <v-card-text>



            <v-flex>
        
            

                      <v-menu
                        ref="menuFecha1"
                        v-model="menuFecha1"
                        :close-on-content-click="false"
                        :return-value.sync="date"
                        transition="scale-transition"
                    
                        offset-y
                        min-width="290px"
                      >
                            <template v-slot:activator="{ on, attrs }">
                              <v-text-field
                                v-model="fecha_inicio"
                                label="Desde"
                                prepend-icon="event"
                                readonly
                                v-bind="attrs"
                                v-on="on"
                              ></v-text-field>
                            </template>
                            <v-date-picker v-model="fecha_inicio" locale="es"
                            no-title  @input="menuFecha1 = false" scrollable>
                            </v-date-picker>
                      </v-menu>
                </v-flex>

            <v-flex>
                  <v-menu
                    ref="menuFecha2"
                    v-model="menuFecha2"
                    :close-on-content-click="false"
                    :return-value.sync="date"
                    transition="scale-transition"
                
                    offset-y
                    min-width="290px"
                  >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="fecha_fin"
                            label="Hasta"
                            prepend-icon="event"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker v-model="fecha_fin" locale="es"
                        no-title  @input="menuFecha2 = false" scrollable>
                        </v-date-picker>
                  </v-menu>

                </v-flex>

            
                  <v-flex justify-center d-flex>
                    
                        <v-btn color="opcion2" dark @click="listar()" style="margin-right:10px;">Generar</v-btn>
                      <v-btn  @click="dialogInicio=false" style="margin-right:10px;">Cancelar</v-btn>

                </v-flex>
    
          </v-card-text>
        
        </v-card>
    </v-dialog>







  <v-dialog v-model="dialog" persistent max-width="1000px">
          <v-card>

              <v-toolbar color="opcion1">
              <v-toolbar-title class="white--text">
                {{ formTitle }}
              </v-toolbar-title>
              <v-spacer></v-spacer>
              <v-icon dark @click="dialog=false">mdi-close</v-icon>
            </v-toolbar>



            <v-card-text>
              <v-container>
                <v-row>
                       <v-col cols="12" sm="3" md="3" lg="3" xl="2">
                         <v-text-field v-model="editedItem.id_equipo"  prepend-icon="mdi-barcode" label="ID equipo"></v-text-field>
                      </v-col>


                        <v-col cols="12" sm="3" md="3" lg="3" xl="2">
                         <v-text-field v-model="editedItem.nombre_codigo"  prepend-icon="mdi-subtitles-outline" label="Nombre - codigo"></v-text-field>
                      </v-col>


                       <v-col cols="12" sm="3" md="3" lg="3" xl="2">
                         <v-text-field v-model="editedItem.placa"  prepend-icon="mdi-tablet" label="Placa"></v-text-field>
                      </v-col>


                       <v-col  cols="12" sm="3" md="3" lg="3" xl="2">
                  
                              <v-autocomplete
                                v-model="editedItem.grupo"
                                :items="items_grupos"
                                label="Grupo"
                                item-value="value"
                                item-text="text"
                                persistent-hint
                                prepend-icon="mdi-select-group"
                                :rules="[rules.required]"
                                :error-messages="errorMessages"
                              >
                              </v-autocomplete>
                      </v-col>

                      
                    <v-col cols="12" sm="3" md="3" lg="3" xl="2">
                        <v-select
                                v-model="editedItem.id_tipo_vehiculo"
                                :items="tiposVehiculosArray"
                                prepend-icon="mdi-train-car"
                                :rules="[rules.required]"
                                :error-messages="errorMessages"
                                label="Tipo Vehiculo"
                            ></v-select>
                      </v-col>


                       <v-col cols="12" sm="3" md="3" lg="3" xl="2">
                        <v-select
                                v-model="editedItem.id_tipo_equipo"
                                :items="tipoEquiposArray"
                                prepend-icon="mdi-devices"
                                :rules="[rules.required]"
                                :error-messages="errorMessages"
                                label="Tipo Equipo Gps"
                            ></v-select>
                      </v-col>


                       <v-col cols="12" sm="3" md="3" lg="3" xl="2">
                        <v-select
                                v-model="editedItem.id_tipo_ticket"
                                :items="tiposTicketsArray"
                                prepend-icon="mdi-ticket-confirmation"
                                :rules="[rules.required]"
                                :error-messages="errorMessages"
                                label="Tipo Ticket"
                            ></v-select>
                      </v-col>

                      <v-col cols="12" sm="3" md="3" lg="3" xl="2">
                          <v-datetime-picker label="Fecha Abierto Ticket" v-model="editedItem.fecha_reportado"> </v-datetime-picker>
                        </v-col>


                    <v-col cols="12" sm="8" md="8" lg="8" xl="8">
                      <v-text-field v-model="editedItem.descripcion"  prepend-icon="mdi-comment" label="Descripcion"></v-text-field>
                    </v-col>

                    <v-col cols="12" sm="3" md="3" lg="3" xl="2">
                        <v-select
                                v-model="editedItem.id_tipo_trabajo"
                                :items="tiposTrabajosArray"
                                prepend-icon="mdi-label"
                                :rules="[rules.required]"
                                :error-messages="errorMessages"
                                label="Tipo Trabajo"
                            ></v-select>
                      </v-col>




                    


                      <v-col  v-if="editedIndex !=-1" cols="12" sm="3" md="3" lg="3" xl="2">
                        <v-select
                               :clearable="true"
                                v-model="editedItem.accion_correcion"
                                :items="accionesCorrecionArray"
                                prepend-icon="mdi-tools"
                                :rules="[rules.required]"
                                :error-messages="errorMessages"
                                label="Accion realizada"
                            ></v-select>
                      </v-col>


                          <v-col  v-if="editedIndex !=-1" cols="4" sm="4" md="4" lg="4" xl="4">
                        <v-select
                               :clearable="true"
                                v-model="editedItem.id_etiqueta"
                                :items="etiquetasArray"
                                prepend-icon="mdi-label"
                                :rules="[rules.required]"
                                :error-messages="errorMessages"
                                label="Etiqueta"
                            ></v-select>
                      </v-col>

                       <v-col v-if="editedIndex !=-1" cols="12" sm="4" md="3" lg="3" xl="3">
                          <v-datetime-picker label="Fecha Cierre Ticket" v-model="editedItem.fecha_solucion"> </v-datetime-picker>
                        </v-col>


                      <v-col cols="6" sm="6" md="6">
                        <v-textarea
                         v-model="editedItem.comentario_atc" 
                         prepend-icon="mdi-comment-account"
                         clear-icon="mdi-close-circle"
                         rows="1"
                         label="Comentario atc"></v-textarea>
                      </v-col>

                      <v-col  v-if="editedIndex !=-1" cols="6" sm="6" md="6">
                        <v-textarea
                         v-model="editedItem.comentario_tecnico" 
                         prepend-icon="mdi-comment"
                         clear-icon="mdi-close-circle"
                         rows="1"
                         label="Comentario tecnico"></v-textarea>
                      </v-col>

                        <v-col v-if="editedIndex !=-1" cols="12" sm="3" md="3" lg="3" xl="2">
                        <v-select
                               :clearable="true"
                                v-model="editedItem.id_tecnico"
                                :items="tecnicosArray"
                                prepend-icon="mdi-account"
                                :rules="[rules.required]"
                                :error-messages="errorMessages"
                                label="Tecnico"
                            ></v-select>
                      </v-col>

                    <v-col v-if="editedIndex !=-1" cols="12" sm="3" md="3" lg="3" xl="2">
                          <v-datetime-picker label="Fecha llegada tecnico" v-model="editedItem.fecha_llegada_tenico"> </v-datetime-picker>
                    </v-col>

                      <v-col v-if="editedIndex !=-1" cols="12" sm="3" md="3" lg="3" xl="2">
                      <v-datetime-picker label="Fecha Inicio Tecnico" v-model="editedItem.fecha_inicio_tenico"> </v-datetime-picker>
                    </v-col>

                    <v-col v-if="editedIndex !=-1" cols="12" sm="3" md="3" lg="3" xl="2">
                      <v-datetime-picker label="Fecha Fin Tecnico" v-model="editedItem.fecha_fin_tecnico"> </v-datetime-picker>
                    </v-col>

                       <v-col v-if="editedIndex !=-1"  cols="12" sm="12" md="12">
                        <span><v-icon class="mr-1">confirmation_number</v-icon><strong>Estado Ticket</strong></span><br>
                        <input type="radio" :value="0" v-model="editedItem.status" class="mt-2"> Abierto
                        <input type="radio" :value="1" v-model="editedItem.status" class="ml-2 mt-2"> Solucionado
                        <input type="radio" :value="2" v-model="editedItem.status" class="ml-2"> Cerrado Vehiculo no disponible
                   </v-col>
                   

                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
              <v-btn color="opcion2" dark @click="save">Guardar</v-btn>
            </v-card-actions>
          </v-card>
    </v-dialog>


    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">deseas deshabilitar Ticket?</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
          <v-btn color="opcion1" text @click="deleteItemConfirm">OK</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>

</v-card>
</template>


<script>

import axios from 'axios';
import { mapMutations, mapState } from "vuex";
import moment from 'moment';


  export default {
    data: () => ({
      ticketsArray:[],
    
       search:'',
       dialogInicio:false,
       dialog: false,
       dialogDelete: false,
       headers: [
     
        { text: 'Numero ticket', value: 'numero_ticket' },
        { text: 'ID equipo', value:'id_equipo'},
        { text: 'Nombre - codigo',value:'nombre_codigo' },
       
        { text: 'Grupo', value: 'grupo' },
        { text:'Tipo vehiculo', value:'id_tipo_vehiculo.tipo_vehiculo'},

        { text:'Tipo ticket', value:'id_tipo_ticket.nombre_tipo_ticket'},
        { text:'Fecha reportado', value:'fecha_reportado'},

        { text:'Tipo trabajo', value:'id_tipo_trabajo.nombre_trabajo'},
        { text:'Descripcion', value:'descripcion'},

        { text:'Comentario Atc', value:'comentario_atc'},

        { text:'Fecha Cerrado', value:'fecha_solucion'},
        { text:'Horas espera ticket', value:'horas_espera_ticket'},
        { text:'Cumple SLA',value:'cumple_sla'},
        { text:'Estatus', value:'status'},

        { text:'Accion correcion',value:'accion_correcion'},
        { text:'Etiqueta',value:'id_etiqueta.nombre_etiqueta'},
        { text:'Comentario tecnico', value:'comentario_tecnico'},

        { text:'llegada tecnico', value:'fecha_llegada_tenico'},
        { text:'Inicio tecnico', value:'fecha_inicio_tenico'},
        { text:'Fin tecnico', value:'fecha_fin_tecnico'},
        { text:'Horas espera tecnico', value:'horas_espera_tecnico'},





        { text: 'Creado', value: 'registrado' },
        { text: 'Acciones', value: 'actions', sortable: false },
      ],

      editedIndex: -1,

      items_grupos:[
            {'value':'Maquinaria', 'text':'Maquinaria'},
            {'value':'Maquinaria_Amarilla', 'text':'Maquinaria_Amarilla'},
            {'value':'Autobuses', 'text':'Autobuses'},
            {'value':'Cisternas', 'text':'Cisternas'},
            {'value':'Motocicletas', 'text':'Motocicletas'},
            {'value':'Otros', 'text':'Otros'}
      ],

      
      accionesCorrecionArray:[ 
                      {'value':'Envio_comandos', 'text':'Envio comandos'},
                      {'value':'Revision_fisica', 'text':'Revision fisica'},
                      {'value':'', 'text':''}
                      ],



      editedItem: {
        numero_ticket:'',
        id_equipo:'',
        nombre_codigo:'',
        placa:'',
        grupo:'',

        id_tipo_vehiculo:'', 
        id_tipo_equipo:'', //starlink, sf, one, basico
        id_tipo_ticket:'', //reporte de fallas o soliciutd de trabajo
        descripcion:'',
        fecha_reportado:new Date(),

        id_tipo_trabajo:'', //Revision, traslado, 
        id_tecnico:'',

        accion_correcion:'', //  'Envio comandos', 'Revision fisica'  existe solo si es revision
        id_etiqueta:'',  //equipo manipulado,  fusible quemado, etc

        fecha_solucion:new Date(),

        comentario_atc:'', //comentario de cindy
        comentario_tecnico:'', //comentario de tecnicos

        status: 0, //0 -> pendiente , 1 -> solucionado, 3-> vehiculo no disponible, cerrado 
        horas_espera_ticket:0,
        cumple_sla:1, // 1-> si cumple  0-> no cumple

        fecha_llegada_tenico:new Date(),
        fecha_inicio_tenico:new Date(),
        fecha_fin_tecnico:new Date(),
        horas_espera_tecnico:0,
        activo: 1,
        creado:new Date()
      },
      defaultItem: {
        numero_ticket:'',
        id_equipo:'',
        nombre_codigo:'',
        placa:'',
        grupo:'',

        id_tipo_vehiculo:'', 
        id_tipo_equipo:'', //starlink, sf, one, basico
        id_tipo_ticket:'', //reporte de fallas o soliciutd de trabajo
        descripcion:'',
        fecha_reportado:new Date(),

        id_tipo_trabajo:'', //Revision, traslado, 
        id_tecnico:'',

        accion_correcion:'', //  'Envio comandos', 'Revision fisica'  existe solo si es revision
        id_etiqueta:'',  //equipo manipulado,  fusible quemado, etc

        fecha_solucion:new Date(),

        comentario_atc:'', //comentario de cindy
        comentario_tecnico:'', //comentario de tecnicos

        status: 0, //0 -> pendiente , 1 -> solucionado, 3-> vehiculo no disponible, cerrado 
        horas_espera_ticket:0,
        cumple_sla:1, // 1-> si cumple  0-> no cumple

        fecha_llegada_tenico:new Date(),
        fecha_inicio_tenico:new Date(),
        fecha_fin_tecnico:new Date(),
        horas_espera_tecnico:0,
        activo: 1,
        creado:new Date()
      },


      errorMessages: '',
      valid: true,
      rules: {
          min3Chars: (value) => value.length >= 3 || "Min. 3 characters",
          required: (value) => !!value || "Campo Requerido.",
          email: (value) => {
                              const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                              return pattern.test(value) || "Invalid e-mail.";
                            },
          },

      tiposVehiculosArray:[],
      tipoEquiposArray:[],
      tiposTicketsArray:[],
      tiposTrabajosArray:[],
      tecnicosArray:[],
      etiquetasArray:[],

       //opciones del dialogo de fechas
        menu: false,
        dialogInicio:false,
        menuFecha1:false,
        fecha_inicio:new Date().toISOString().substr(0, 10),
        menuFecha2:false,
        fecha_fin:new Date().toISOString().substr(0, 10),
        date: new Date().toISOString().substr(0, 10),


      column_excel:[

            {label: "Numero ticket",  field: "numero_ticket"},
            {label: "Id equipo",  field: "id_equipo"},
            {label: "Nombre codigo", field: "nombre_codigo"},
            {label: "Placa", field: "placa"},
            {label: "Tipo vehiculo", field: "grupo"},
            {label: "Hora fin", field: "id_tipo_vehiculo.tipo_vehiculo"},
            {label: "Tipo ticket", field:"id_tipo_ticket.nombre_tipo_ticket"},
            {label: "Fecha reportado", field:"fecha_reportado_"},
            {label: "Tipo trabajo", field:"id_tipo_trabajo.nombre_trabajo"},
            {label: "Descripcion", field:"descripcion"},
            {label: "Comentario_atc", field:"comentario_atc"},
            {label: "Fecha cierre", field:"fecha_solucion_"},

            {label: "Comentario tecnico", field:"comentario_tecnico"},
           
            {label: "Etiqueta", field:"id_etiqueta.nombre_etiqueta"},
            {label: "Estatus", field:"status"},
            
            {label: "Horas espera ticket", field:"horas_espera_ticket"},
            {label: "Cumple SLA", field:"cumple_sla"},

            {label:"Fecha llegada tecnico", field:"fecha_llegada_tenico_"},
            {label:"Fecha inicio tecnico", field:"fecha_inicio_tenico_"},
            {label:"Fecha fin tecnico", field:"fecha_fin_tecnico_"},
            {label:"Horas espera tecnico", field:"horas_espera_tecnico"},
          
            {label: "Registrado", field:"registrado"}
      ],



    }),

    computed: {

       ...mapState(['usuario', 'token']),

      formTitle () {
        return this.editedIndex === -1 ? 'Nuevo Ticket' : 'Edicion Ticket'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {

      let fecha=new Date();
      fecha.toLocaleString('en-US', { timeZone: 'America/Managua'});
      let dia_hoy=fecha.getDate();
      if(dia_hoy<10)dia_hoy='0'+dia_hoy;
      let mes=fecha.getMonth()+1;
      if(mes<10)mes='0'+mes;
      if(dia_hoy=='00'){dia_hoy='01';}

      this.fecha_fin =fecha.getFullYear()+'-'+mes+'-'+dia_hoy;

      this.fecha_inicio=this.CalcularFechasemanaPasada();



      this.listar();
      this.listarTipoVehiculo();
      this.listarTipoEquipos();
      this.listarTipoTicket();
      this.listarTipoTrabajo();
      this.listarTecnicos();
      this.listarEtiquetas();
      
    },

    methods: {

       ...mapMutations(['mostrarLoading','ocultarLoading']),

    listarTipoVehiculo(){

          this.tiposVehiculosArray=[];
          let config={headers:{token:this.token}};
            this.mostrarLoading({titulo:'accediendo a datos'});
              let me=this;

            axios.get('tipovehiculos', config
            ).then(function(response){
                //console.log(response);

                let respuesta=response.data;
                respuesta.map(function(x){
                                          me.tiposVehiculosArray.push({'text':x.tipo_vehiculo, 'value':x._id});
                        });

                me.ocultarLoading();
            }).catch(function(error){
              console.log(error);
            });
      },

       listarTipoEquipos(){

          this.tipoEquiposArray=[];

            let config={headers:{token:this.token}};
          this.mostrarLoading({titulo:'accediendo a datos'});
            let me=this;

            axios.get('equipos', config
            ).then(function(response){
                let respuesta=response.data;

                  respuesta.map(function(x){
                      me.tipoEquiposArray.push({'text':x.nombre_equipo, 'value':x._id});
                    });

                me.ocultarLoading();
            }).catch(function(error){
              console.log(error);
            });
      },

       listarTipoTicket(){

        this.tiposTicketsArray=[];
          let config={headers:{token:this.token}};
          this.mostrarLoading({titulo:'accediendo a datos'});
            let me=this;

            axios.get('tipotickets', config
            ).then(function(response){
              
                let respuesta=response.data;
                  respuesta.map(function(x){
                      me.tiposTicketsArray.push({'text':x.nombre_tipo_ticket, 'value':x._id});
                    });
                me.ocultarLoading();
            }).catch(function(error){
              console.log(error);
            });
      },

        listarTipoTrabajo(){

         this.tiposTrabajosArray=[];

         let config={headers:{token:this.token}};
         this.mostrarLoading({titulo:'accediendo a datos'});
          let me=this;

          axios.get('trabajos', config
          ).then(function(response){
             
               let respuesta=response.data;
                 respuesta.map(function(x){
                     me.tiposTrabajosArray.push({'text':x.nombre_trabajo, 'value':x._id});
                  });

              me.ocultarLoading();
          }).catch(function(error){
            console.log(error);
          });

      },

       listarTecnicos(){

          this.tecnicosArray=[];

          let config={headers:{token:this.token}};
          this.mostrarLoading({titulo:'accediendo a datos'});
          let me=this;

          axios.get('tecnicos', config
          ).then(function(response){

             let respuesta=response.data;
                 respuesta.map(function(x){
                     me.tecnicosArray.push({'text':x.nombre_tecnico, 'value':x._id});
                  });

              me.ocultarLoading();
          }).catch(function(error){
            console.log(error);
          });
      },

       listarEtiquetas(){

        this.etiquetasArray=[];

         let config={headers:{token:this.token}};
         this.mostrarLoading({titulo:'accediendo a datos'});
          let me=this;

          axios.get('etiquetas', config
          ).then(function(response){

             let respuesta=response.data;
                 respuesta.map(function(x){
                     me.etiquetasArray.push({'text':x.nombre_etiqueta, 'value':x._id});
                  });
            
              me.ocultarLoading();
          }).catch(function(error){
            console.log(error);
          });


       },


        listar(){

          this.ticketsArray=[];

          let config={headers:{token:this.token}};
         this.mostrarLoading({titulo:'accediendo a datos'});
          let me=this;

          axios.post('tickets-fechas',
          {
            'desde':new Date(this.fecha_inicio).toISOString().substr(0, 10),
            'hasta':new Date(this.fecha_fin).toISOString().substr(0, 10) 
          },
          config
          ).then(function(response){

            let respuesta=response.data;
            respuesta.map(function(x){

              x.fecha_reportado_=me.getDate(x.fecha_reportado);
              x.fecha_solucion_=me.getDate(x.fecha_solucion);

              if((x.fecha_llegada_tenico) && (x.fecha_llegada_tenico!='')){
                x.fecha_llegada_tenico_=me.getDate(x.fecha_llegada_tenico);
              }else{
                x.fecha_llegada_tenico='';
                x.fecha_llegada_tenico_='';
              }

              if((x.fecha_inicio_tenico) && (x.fecha_inicio_tenico!='')){
                  x.fecha_inicio_tenico_=me.getDate(x.fecha_inicio_tenico);
              }else{
                x.fecha_inicio_tenico_='';
                x.fecha_inicio_tenico='';
              }

              if((x.fecha_fin_tecnico) && (x.fecha_fin_tecnico!='')){
                   x.fecha_fin_tecnico_=me.getDate(x.fecha_fin_tecnico);
              }else{
                 x.fecha_fin_tecnico_='';
                 x.fecha_fin_tecnico='';
              }

                if((!x.id_etiqueta) || (!x.id_etiqueta.nombre_etiqueta)){
                    x.id_etiqueta={'nombre_etiqueta':''};
                }

            
                me.ticketsArray.push(x);

            });
            
              me.dialogInicio=false;
              me.ocultarLoading();
          }).catch(function(error){
            console.log(error);
          });

      },

       editItem(item){

            console.log(item);

            this.editedIndex = this.ticketsArray.indexOf(item);
            this.editedItem = Object.assign({}, item);

            console.log('indice:'+  this.editedIndex);

            this.editedItem.id_tipo_vehiculo=item.id_tipo_vehiculo._id;
            this.editedItem.id_tipo_equipo=item.id_tipo_equipo._id;
            this.editedItem.id_tipo_ticket=item.id_tipo_ticket._id;
            this.editedItem.id_tipo_trabajo=item.id_tipo_trabajo._id;

            this.editedItem.fecha_reportado=new Date(item.fecha_reportado);


            if(item.fecha_llegada_tenico && item.fecha_llegada_tenico!=''){ this.editedItem.fecha_llegada_tenico=new Date(item.fecha_llegada_tenico);}
            if(item.fecha_inicio_tenico && item.fecha_inicio_tenico!=''){ this.editedItem.fecha_inicio_tenico=new Date(item.fecha_inicio_tenico);}
            if(item.fecha_fin_tecnico && item.fecha_fin_tecnico!=''){ this.editedItem.fecha_fin_tecnico=new Date(item.fecha_fin_tecnico);}

           if(item.fecha_solucion && item.fecha_solucion!=''){ this.editedItem.fecha_solucion=new Date(item.fecha_solucion);}

         
            this.dialog=true;
      },

      deleteItem(item){
        this.editedIndex = this.ticketsArray.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true;
      },

      deleteItemConfirm(){


            let id=this.editedItem._id;
            let config={headers:{token:this.token}};
            this.mostrarLoading({titulo:'accediendo a datos'});

             axios.put(`tickets/${id}`, 
                                {'activo':0},
                                config
                              ).then(response=>{
                               
                                 this.closeDelete();
                                 this.ocultarLoading();
                                 console.log(response);
                                 this.listar();

                              }).catch(e=>{
                                    console.log(e)
                              });
        },

          close(){
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete(){
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },


        save () {

          if((this.editedItem.id_equipo!='')&&(this.editedItem.nombre_codigo!='')&&(this.editedItem.id_tipo_vehiculo!='')
          &&(this.editedItem.id_tipo_ticket!='')&&(this.editedItem.id_tipo_equipo!='')&&(this.editedItem.id_tipo_trabajo!='')){


             if(this.editedIndex > -1) {

              if((this.editedItem.accion_correcion!='')&&(this.editedItem.fecha_solucion!='')){

           
               let id=this.editedItem._id;
               this.mostrarLoading({titulo:'accediendo a datos'});
               let config={headers:{token:this.token}};

               let item_editar={
                                    'id_equipo':this.editedItem.id_equipo,
                                    'nombre_codigo':this.editedItem.nombre_codigo,
                                    'placa':this.editedItem.placa,
                                    'grupo':this.editedItem.grupo,

                                    'id_tipo_vehiculo':this.editedItem.id_tipo_vehiculo, 
                                    'id_tipo_equipo':this.editedItem.id_tipo_equipo, //starlink, sf, one, basico
                                    'id_tipo_ticket':this.editedItem.id_tipo_ticket, //reporte de fallas o soliciutd de trabajo
                                    'descripcion':this.editedItem.descripcion,
                                    'fecha_reportado':this.editedItem.fecha_reportado,

                                    'id_tipo_trabajo':this.editedItem.id_tipo_trabajo, //Revision, traslado, 
                                    
                                    'accion_correcion':this.editedItem.accion_correcion, //  'Envio comandos', 'Revision fisica'  existe solo si es revision
                                 

                                    'comentario_atc':this.editedItem.comentario_atc, //comentario de cindy
                                    'comentario_tecnico':this.editedItem.comentario_tecnico, //comentario de tecnicos

                                    'status': this.editedItem.status, //0 -> pendiente , 1 -> solucionado, 3-> vehiculo no disponible, cerrado 
                                   
                                    'fecha_llegada_tenico':this.editedItem.fecha_llegada_tenico,
                                    'fecha_inicio_tenico':this.editedItem.fecha_inicio_tenico,
                                    'fecha_fin_tecnico':this.editedItem.fecha_fin_tecnico
               };



               if(this.editedItem.id_tecnico!=''){ item_editar.id_tecnico=this.editedItem.id_tecnico; }
               if((this.editedItem.id_etiqueta!='')&&(this.editedItem.id_etiqueta.nombre_etiqueta!='')){ item_editar.id_etiqueta=this.editedItem.id_etiqueta; }  //equipo manipulado,  fusible quemado, etc

                if(this.editedItem.fecha_solucion!=''){ 
                        item_editar.fecha_solucion=this.editedItem.fecha_solucion; 

                        let dateA = moment(this.editedItem.fecha_reportado);
                        let dateB = moment(this.editedItem.fecha_solucion);

                        let diferenciaMILs=dateB.diff(dateA);
                        diferenciaMILs=diferenciaMILs/1000;
                        diferenciaMILs=diferenciaMILs/3600;

                        if(parseFloat(diferenciaMILs)>0){
                            item_editar.horas_espera_ticket=parseFloat(diferenciaMILs).toFixed(2);

                            if(parseFloat(diferenciaMILs)>16){
                                item_editar.cumple_sla=0; //no cumple' 1-> si cumple  0-> no cumple
                            }else{
                                item_editar.cumple_sla=1;
                            }
                        }
                  }
                  
                  

                  if((this.editedItem.fecha_llegada_tenico!='') && (this.editedItem.fecha_inicio_tenico!='')){
                        let dateA = moment(this.editedItem.fecha_llegada_tenico);
                        let dateB = moment(this.editedItem.fecha_inicio_tenico);

                         let diferenciaMILs=dateB.diff(dateA);
                         diferenciaMILs=diferenciaMILs/1000;
                         diferenciaMILs=diferenciaMILs/3600;

                         if(parseFloat(diferenciaMILs)>0){
                          item_editar.horas_espera_tecnico=parseFloat(diferenciaMILs).toFixed(2);
                         }


                  }





                axios.put(`tickets/${id}`, item_editar,
                                config
                              ).then(response=>{
                                this.close();
                                this.ocultarLoading();
                                console.log(response);
                                this.listar();

                              }).catch(e=>{
                                    console.log(e)
                              });

                   }else{
                       alert('faltan campos de accion o fecha cerrada')
                    }

           }else{  //nuevo

            let config={headers:{token:this.token}};
            this.mostrarLoading({titulo:'accediendo a datos'});

             axios.post('nuevo-ticket',{ 
                                    'id_equipo':this.editedItem.id_equipo,
                                    'nombre_codigo':this.editedItem.nombre_codigo,
                                    'placa':this.editedItem.placa,
                                    'grupo':this.editedItem.grupo,

                                    'id_tipo_vehiculo':this.editedItem.id_tipo_vehiculo, 
                                    'id_tipo_equipo':this.editedItem.id_tipo_equipo, //starlink, sf, one, basico
                                    'id_tipo_ticket':this.editedItem.id_tipo_ticket, //reporte de fallas o soliciutd de trabajo
                                    'descripcion':this.editedItem.descripcion,
                                    'fecha_reportado':this.editedItem.fecha_reportado,

                                    'id_tipo_trabajo':this.editedItem.id_tipo_trabajo, //Revision, traslado, 
                                    'comentario_atc':this.editedItem.comentario_atc, //comentario de atc
                                    },
                                   
              config
          ).then(response=>{
             this.close();
            console.log(response);
            this.listar();
            this.ocultarLoading();

          }).catch(e=>{
                console.log(e)
          });



           }



          }else{

            alert('faltan campos');
          }
         



        },


       getDate(evaluar) {
          let date = new Date(evaluar),
          year = date.getFullYear(),
          month = (date.getMonth() + 1).toString(),
          formatedMonth = (month.length === 1) ? ("0" + month) : month,
          day = date.getDate().toString(),
          formatedDay = (day.length === 1) ? ("0" + day) : day,
          hour = date.getHours().toString(),
          formatedHour = (hour.length === 1) ? ("0" + hour) : hour,
          minute = date.getMinutes().toString(),
          formatedMinute = (minute.length === 1) ? ("0" + minute) : minute,
          second = date.getSeconds().toString(),
           formatedSecond = (second.length === 1) ? ("0" + second) : second;
          return formatedDay + "-" + formatedMonth + "-" + year  +' '+formatedHour+':'+formatedMinute+':'+formatedSecond;
      },


        CalcularFechasemanaPasada(){
          var fecha=new Date((new Date()).valueOf() - 1000*60*60*24*5); //5 dias
          var mes=fecha.getMonth()+1;
          if(mes<10)mes='0'+mes;
          var dia=fecha.getDate();
          if(dia<10)dia='0'+dia;
          var textofecha=fecha.getFullYear()+'-'+mes+'-'+dia;

          return(textofecha);
          },

          IterarFechas(desde, hasta){

          //console.log('iteramos:'+desde+', hasta:'+hasta);
          
            let ArrayFechas_return=[];
          
            if(new Date(desde).toLocaleDateString()==new Date(hasta).toLocaleDateString()	){
                ArrayFechas_return.push(desde);
          
                console.log('mismo dia');
            }else{
          
          
                ArrayFechas_return.push(desde);

          
          
                let procesar=true;
                let horas=24;
              
                while(procesar){
              
                let ayer=new Date(desde.getTime() + horas*60*60*1000);
                var mes=ayer.getMonth()+1;
                if(mes<10)mes='0'+mes;
                var dia=ayer.getDate();
                if(dia<10)dia='0'+dia;
                var textofecha=ayer.getFullYear()+'-'+mes+'-'+dia;
                
                  
                  if(hasta.toISOString().slice(0,10)==new Date(textofecha).toISOString().slice(0,10)){
                    procesar=false;
                  }else{
                      //validamos hasta ayer-- hoy no debe ser validado
                    if(new Date(textofecha).toISOString().slice(0,10)==new Date().toISOString().slice(0,10)){
                    procesar=false;
                    }else{
                    ArrayFechas_return.push(new Date(textofecha+' 00:00:00'));
                    // console.log('añadimos:'+new Date(textofecha).toISOString().slice(0,10));
                    }
              
                  
              
                  }
                
                
                horas=horas+24
                }//finde procesar

                ArrayFechas_return.push(hasta);
          
          
            }//fin si hay varios dias
          
          
            return (ArrayFechas_return);
          
          
          }

      






    }
  }
</script>


